import { SyntheticEvent, useState } from 'react';

export const useLoadedImages = (): [boolean, (e: SyntheticEvent<HTMLImageElement>) => void] => {
  const [isLoadedImage, setLoadedStatus] = useState(true);

  const handleLoadImage = () => {
    setLoadedStatus(true);
  };

  return [isLoadedImage, handleLoadImage];
};
